const albums = [
  {
    name: '手绘册',
    items: [],
  },
  {
    name: '板绘册',
    items: [],
  },
];

for (let i = 0; i < 30; i += 1) {
  const pathBase = './static/images/手绘/';
  const image = `${pathBase}${i + 1}.jpg`;
  albums[0].items.push(image);
}

for (let i = 0; i < 15; i += 1) {
  const pathBase = './static/images/板绘/';
  const image = `${pathBase}${i + 1}.jpg`;
  albums[1].items.push(image);
}

export default albums;



