<template>
  <div class="albums">
    <h2 class="album-title">作品专辑</h2>
    <div class="grid">
      <router-link :to="`/album/${index + 1}`" class="item" v-for="(item, index) in albums" :key="`album-${index}`">
        <h3 class="item-title">{{item.name}}</h3>
        <div class="item-cover" :style="{
          backgroundImage: `url(${item.items[0]}`,
        }">
        </div>
      </router-link>
    </div>
  </div>
</template>

<script>
  import albums from '@/data';

  export default {
    name: 'Albums',
    data() {
      return {
        albums,
      };
    },
  };
</script>

<style lang="scss" scoped>
  .albums {
    .album-title {
      display: block;
      color: #3e3e3e;
      font-size: 26px;
      font-weight: 700;
      margin: 40px 0;
      letter-spacing: 2px;
      text-transform: uppercase;
      text-align: center;
    }

    .grid {
      position: relative;
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      justify-content: center;
      margin: 0 auto;
      width: 100%;
      max-width: 960px;
      box-sizing: content-box;
    }

    .item {
      display: block;
      width: 300px;
      height: 380px;
      margin: 10px;
      z-index: 1;
      border-radius: 4px;
      overflow: hidden;
    }

    .item-cover {
      position: relative;
      width: 100%;
      height: 300px;
      border-radius: 4px;
      background: #ccc no-repeat center;
      background-size: cover;
    }

    .item-title {
      color: #999CA0;
      font-size: 18px;
      line-height: 24px;
      text-align: center;
      text-decoration: underline;
    }
  }
</style>




