<template>
  <div class="album">
    <div class="album-title">专辑详情-{{name}}</div>
    <div class="grid preview-img-list">
      <a data-fancybox="gallery" :href="item" class="item preview-img-item" v-for="(item, index) in pictures" :key="`album-${index}`">
        <div class="item-cover" :style="{
          backgroundImage: `url(${item}`,
        }">
        </div>
      </a>
    </div>
  </div>
</template>

<script>
  import {
    get,
  } from 'lodash';

  import data from '@/data';

  export default {
    name: 'Album',
    data() {
      return {
        name: '',
        pictures: [],
      };
    },
    created() {
      const params = this.$route.params;
      const { id } = params;
      const pictures = get(data, `${id - 1}.items`) || [];
      const name = get(data, `${id - 1}.name`) || '';
      this.pictures = pictures;
      this.name = name;
    },
    mounted() {
      // eslint-disable-next-line
      $('[data-fancybox]').fancybox({
      });
    },
  };
</script>

<style lang="scss" scoped>
  .album {
    .album-title {
      display: block;
      color: #3e3e3e;
      font-size: 26px;
      font-weight: 700;
      margin: 40px 0;
      letter-spacing: 2px;
      text-transform: uppercase;
      text-align: center;
    }

    .grid {
      position: relative;
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      justify-content: center;
      margin: 0 auto;
      width: 100%;
      max-width: 960px;
      box-sizing: content-box;
    }

    .item {
      display: block;
      width: 300px;
      height: 300px;
      margin: 10px;
      z-index: 1;
      border-radius: 4px;
      overflow: hidden;
    }

    .item-cover {
      position: relative;
      width: 100%;
      height: 300px;
      border-radius: 4px;
      background: #ccc no-repeat center;
      background-size: cover;
    }
  }
</style>





